.section {
  width: 100%;
  height: 830px;
  display: flex;
  gap: 30px;
}

.left {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 9px;
  margin: 50px;
}

.title {
  font-size: 2rem;
  font-weight: bold;
  color: #08479e;
  text-shadow: 1px 1px rgb(163, 163, 163);
}

.Doctor {
  margin-right: 10px;
  color: #08479e;
  text-shadow: 1px 1px rgb(163, 163, 163);
}

.h1 {
  font-size: 1.3rem;
  font-weight: bold;
  margin-top: 10px;
  color: rgb(57, 57, 57);
  text-shadow: 1px 1px rgb(163, 163, 163);
}

.p {
  font-size: 1.2rem;
  color: rgb(76, 76, 76);
}

.right {
  display: flex;
  align-items: center;
}

.img04, .img05, .img06 {
  width: 250px;
  height: 250px;
}

.img05, .img06 {
  margin-left: 10px;
}